  .ready_Quez {
    direction: rtl;
   }
  .ready_Quez::before {
    content: "";
    position: absolute;
    left: 0;
    top: -52px;
    width: 100%;
    height: 100%;
    background-color: var(--bgColor);
    z-index: -1;
    transform: skewY(-6deg);
    transform-origin: top left;
  }
   
   
  .ready_Quez .image img {
    width: 550px;
    position: relative;
    animation: up-and-down 5s linear infinite;
  }
  .ready_Quez .go_Down {
    color: #f26a40;
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
    transition: 0.3s;
  
  }
  .ready_Quez .go_Down .animation{
    animation: bouncing 1.5s infinite;
  }
  .start_Quiz {
    direction: rtl;
  }
  @media only screen and (min-width: 768px) and (max-width: 1280px) {
    .input_Start{
      margin: 0 100px;
  }
}
 
  /* end landing */
  @keyframes up-and-down {
    0% , 
    100% {
      top: 0;
    }
    50% {
        top: -50px;
    }
  }
  @keyframes bouncing {
    0% , 10% , 20% , 50% , 80% , 100% {
        transform: translateY(0);
    }
    40% , 60% {
        transform: translateY(-15px);
    }
  }




















   